.orders{
    width: 100%;
    
    background: rgb(209, 205, 205);
}
.target{
    width: 100%;
    height: 86.7%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.target_order{
    width: 85%;
    height: 90%;
    overflow-y: scroll;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    
}
.box{
    width: 80%;
    min-height: 13rem;
    background: rgb(181, 194, 167);
    margin: 4vh;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    position: relative;
    flex-flow: row wrap;
    overflow: auto;
}
.tipical{
    width: 100%;
    min-height: 12rem;
    display: flex;
    padding-bottom: 1vh;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.span{
    margin-top:1.6vh ;
    max-width: 90%;
}
.btn{
    position: relative;
    top: 0;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 3.5vh;
    background: rgb(86, 146, 27);
    border: 1px solid rgb(86, 146, 27);
    outline: none;
    padding: .5vh .5vh;
}
.btn:hover{
    border: 1px solid rgb(184, 229, 138);
    background: rgb(111, 160, 62);
}

@media(max-width:600px){
    .span{
        font-size: 3vh;
    }
    
}

@media(max-width:430px){
    .span{
        font-size: 3vh;
    }
    .target_order{
        width: 95%;
    }
    .box{
        width: 95%;
      
    }
   
}